import React, { Component } from 'react';
import { Link } from 'react-router';
import './DailyContents.css';

const DisplayResult = ({category,name}) =>
  <div>
    {name}
    <div className={"btn-" + category + " square"}>
       {/*category*/}
    </div>
  </div>

const Result = ({state}) =>
  <div className="box">
    {
      state.products.filter( (product)=> (
        product.category===state.displayCategory||
        state.displayCategory==='all'
        )
      ).map( (product)=>
        <DisplayResult category={product.category} name={product.name}/>
      )
    }
  </div>

const ButtonCategory = ({setCategory, category}) =>
  <button
    className={"btn-" + category}
    onClick={()=>setCategory(category)}>
    {category}
  </button>

const UI = ({setCategory, state, allProducts}) =>
  <div>
    <div className="filterBtn">
      {state.productCategories.map(
        (category)=>
          <ButtonCategory
            setCategory={setCategory}
            category={category} />
      )}
    </div>
    <div>
      <Result state={state}/>
    </div>
  </div>

class DailyContentItems extends Component {
  constructor(props) {
    super(props);
    this.state = {
      displayCategory: 'all',
      products: PRODUCTS,
      productCategories: PRODUCT_CATEGORIES
    };

    this.setCategory = this.setCategory.bind(this);
  }

  setCategory (category) {
    this.setState({
      displayCategory: category
    });
  }

  render(){
    return (
      <div className="daily-contents">
        <div className="daily-header">
          <h2>Daily Life</h2>
        </div>
        <div className="daily-band"></div>
        <div className="daily-body">
          <UI setCategory={this.setCategory} state={this.state}/>
        </div>
      </div>
    );
  }
}

const MenuItem = ({active, children, to}) => (
  <Link to={to}>
    {children}
  </Link>
);

const Data01 = () => (
  <MenuItem to={'/daily/data01'}>
    <div className="daily-item item01">
      <img src={require('../../images/daily/data01_thumb.jpg')} alt="부산 여행 이미지" />
      <p>2017년 3월 25일<br />부산 감천벽화마을과 시장 먹방</p>
      <div className="curtain"></div>
    </div>
  </MenuItem>
);

const Data02 = () => (
  <a href="">
    <div className="daily-item item02">
      <img src='' alt="청산도 여행 이미지" />
      <p>2017년 4월 29일 ~ 5월 2일 (3박 4일)<br />슬로우시티 청산도에 가다</p>
      <div className="curtain"></div>
    </div>
  </a>
);

const Data03 = () => (
  <a href="">
    <div className="daily-item item03">
      <img src='' alt="시애틀과 밴쿠버 여행 이미지" />
      <p>2017년 9월 29일 ~ 10월 6일 (7박 9일)<br />잠 못 이루는 밤 시애틀, 벤쿠버에 가다</p>
      <div className="curtain"></div>
    </div>
  </a>
);

// Category : FOOD, TRAVEL, CULTURE, ETC 등등...
const PRODUCTS = [
  {category: 'TRAVEL', name: <Data03 />},
  {category: 'TRAVEL', name: <Data02 />},
  {category: 'TRAVEL', name: <Data01 />}
];

// get unique categories
const PRODUCT_CATEGORIES = PRODUCTS
  .map((product)=>product.category)
  .filter((item, i, arr) =>arr[i] !== arr[i - 1])
//unique = a.filter((x, i, a) => a.indexOf(x) == i)
PRODUCT_CATEGORIES.push("all")

console.log(PRODUCT_CATEGORIES)

class DailyContents extends Component {
  render(){
    return (
      <DailyContentItems products={PRODUCTS} />
    );
  }
}

export default DailyContents;



