import React, { Component } from 'react';
import Header from '../../Header/Header';
import Contents from '../../Contents/Contents';
import Footer from '../../Footer/Footer';
import '../HistoryContents.css';
import './HistoryData.css';

class Data01 extends Component {
  render(){
    return (
      <div>
        <Header />
        <Contents>
          <Data />
        </Contents>
        <Footer />
      </div>
    );
  }
}

class Data extends Component {
  render(){
    return (
      <div className="history-contents">
        <div className="history-header">
          <h2>History</h2>
        </div>
        <div className="history-band"></div>
        <div className="history-body">

          { /* Data01 start */ }
          <div className="main_text">
            <h2>리액트 프로젝트를 서버에 올린 후 새로고침시 서버 오류 현상</h2>
            <span className="date">2017년 4월 5일</span>
            <p>
              create-react-app을 사용하여 리액트 프로젝트를 만들었다.<br />
              그리고 이것저것 코딩 작업...<br />
              코딩이 끝나고 리액트 프로젝트를 외부 서버에 올리기위해 npm run build 명령어를 통해 build를 했고, 드디어 올리는구나 하는 뿌듯한 마음으로 내가 그나마 써봤던 xampp를 통해 올렸다.<br />
              첫 메인 화면 잘 나타나고 메뉴를 누르니 이동도 완벽 끝!<br />
              그리고 새로고침을 하는데, 헉. 객체 오류가 나타났다.....
            </p>
            <img src={require('../../../images/history/history01/history01_00.jpg')} alt="객체 오류 페이지 이미지" />
            <p>
              이건 뭐지 하는 생각으로 다시 첫 메인화면으로 가서 다시 새로고침하니 또 잘 나온다???<br />
              <br />
              근데 메뉴 이동한 후 새로고침을 하면 헉. 객체 오류... ㅠㅠ 슬프다. 어디서 어떻게 뭐가 잘못된것일까.<br />
              <br />
              폭풍 검색을 시작. babel? webpack 설정? 무슨말인지 하나도 모르겠다.<br />
              <br />
              몇 일을 검색하고 계속 고민하고 했는데 모르겠다ㅠㅠ<br />
              <br />
              그러다 결국 자주 이용하는 카페에 리액트 글을 올리는 분을 붙잡고 다짜고짜 리액트 물어보고 싶다고 컨택을 했고, 그 분은 아주 친절하게도 내게 카톡으로 열심히 답변 해주셨다. 너무 감사했다ㅠㅠ<br />
              <br />
              그분이 내게 알려주신 방법!<br />
              난 리액트 프로젝트를 서버에 올릴 때 xampp (apache 웹서버)를 이용을 했다.<br />
              그러므로 .htaccess 파일을 만들어 URL 처리하는 설정을 다시 해주라는 것이였다.<br />
              <br />
              그래서 build 했던 파일 폴더 안에 .htaccess 파일을 만들어 그 안에 아래와 같은 코드를 작성했다.
            </p>
            <div className="code">
              <pre>
                <code>
{`RewriteEngine on
RewriteBase /

RewriteCond %{REQUEST_FILENAME} !-f
RewriteCond %{DOCUMENT_ROOT} !-f
RewriteRule ^(.+)$ /index.html [L]`}
                </code>
              </pre>
            </div>
            <p>
              사실 무슨뜻인지 모르겠다.<br />
              <br />
              그래서 검색을 했다. 위 코드를 이해하기 위해.<br />
              <br />
              결론부터 말하자면, 위와같이 작성 후 서버에 올리고, 메뉴 이동 후 새로고침을 하니...<br />
              헐,, 새로고침을 해도 화면이 잘 나타났다!<br />
              <br />
              10년 묵은 체증이 확 내려가는 듯한 느낌을 받을 만큼 너무 기뻤다.<br />
              <br />
              휴~ 큰 산을 넘은 것 같다
            </p>

            <div className="source">
              <h3>.htaccess 개념 및 내용 정리 참고 <a href="http://soul0.tistory.com/139" className="url">http://soul0.tistory.com/139</a></h3>

              <p>
                .htaccess 파일(혹은 "분산 설정파일")을 사용하면 디렉토리별로 설정을 변경할 수 있다.
                여러 설정 지시어가 있는 파일을 특정 문서 디렉토리에 두면 그 디렉토리와 모든 하위 디렉토리에 지시어를 적용한다.
              </p>
            </div>

            <div className="tags">
              <img src={require('../../../images/icon/tag.png')} alt="태그 아이콘" />
              <a href="#" rel="tag"><span>#react.js</span></a>
              <a href="#" rel="tag"><span>#리액트</span></a>
              <a href="#" rel="tag"><span>#.htaccess</span></a>
            </div>
          </div>
          { /* Data01 end */ }

        </div>
      </div>
    );
  }
}

export default Data01;



