import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import { Router, Route, browserHistory, IndexRoute } from 'react-router';
import { Home, About, Work, Daily, History, NoMatch } from './components';
import { DailyData01 } from './components';
import { HistoryData01 } from './components';
import './index.css';

ReactDOM.render(
  <Router history={browserHistory}>
    <Route path="/" component={App}>
      <IndexRoute component={Home} />
      <Route path="/" component={Home} />
      <Route path="about" component={About} />
      <Route path="work" component={Work} />
      <Route path="daily" component={Daily} />
      <Route path="daily/data01" component={DailyData01} />
      <Route path="history" component={History} />
      <Route path="history/history01" component={HistoryData01} />
      <Route path="*" component={NoMatch} />
    </Route>
  </Router>,
  document.getElementById('root')
);



