import React, {Component} from 'react';
import Header from '../../components/Header/Header';
import Contents from '../../components/Contents/Contents';
import Footer from '../../components/Footer/Footer';
import './About.css';

class About extends Component {
  render(){
    return (
      <div>
        <Header />
        <Contents>
          <AboutContents />
        </Contents>
        <Footer />
      </div>
    );
  }
}

class AboutContents extends Component {
  render(){
    return (
      <div className="about-contents">
        <div className="about-header">
          <h2>About blog</h2>
        </div>
        <div className="about-band"></div>
        <div className="about-body">
          <div className="description">
            <p>
              이 블로그는 스터디를 통해 접하게된 ReactJS에 흥미를 느껴 만들게 되었다.<br />
              블로그를 통해 프론트엔드 개발자가 갖춰야할 지식들을 하나 하나 공부하는게 목표이다.<br />
              또한 이 블로그는 지식 공유의 목적보다는 개인 만족에 맞춘 블로그이다.<br />
              지극히 개인 만족에 맞춰진 이 블로그에서 다룰 내용은 블로그를 만들면서 겪은 내용들, 그동안 작업했던 결과물, 돈과 시간이 있다면 항상 떠나는 여행 기행 등등 일 것이다. 항상 그때그때 기분에 따라 바뀌기 때문에...
            </p>
          </div>
          <div className="summary">
            <div className="me">
              <h4>Me</h4>
              <ul>
                <li>Web publisher</li>
                <li>female</li>
                <li>20's</li>
              </ul>
            </div>
            <div className="skill">
              <h4>Skills</h4>
              <ul>
                <li>HTML</li>
                <li>CSS</li>
                <li>Javascript</li>
                <li>jQuery</li>
                <li>Bootstrap</li>
              </ul>
            </div>
            <div className="study">
              <h4>Studying</h4>
              <ul>
                <li>ReactJS</li>
                <li>NodeJS</li>
                <li>Javascript Libraries...</li>
                <li>Javascript Frameworks...</li>
              </ul>
            </div>
            <div className="portfolio">
              <h4>Work</h4>
              <ul>
                <li>Samsung Galaxy S series Mobile Manual...</li>
                <li>Company website...</li>
              </ul>
            </div>
            <div className="contact">
              <h4>Contact</h4>
              <ul>
                <li>qkrekwjd9715@naver.com</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default About;



