import React, {Component, PropTypes} from 'react';
import './Contents.css';

class Contents extends Component {
  constructor(props){
    super(props);
    this.state = {
      scrollTop: 0
    };
  }

  componentWillMount(){
    window.addEventListener("scroll", (e) => {
      this.setState({scrollTop: document.body.scrollTop})
    })
  }

  shouldComponentUpdate(){
    let thisScrollTop = this.state.scrollTop;
    const headerId = document.getElementById("header");

    if(thisScrollTop >= 380){
      headerId.className = "active"
    }else{
      headerId.className = " "
    }
  }

  render(){
    return (
      <section id="contents">
        <div className="contents-wrap">{this.props.children}</div>
      </section>
    );
  }
}

Contents.propTypes = {
  children: PropTypes.element.isRequired
};

export default Contents;



