import React, { Component } from 'react';
import { Link } from 'react-router';
import './HistoryContents.css';

const MenuItem = ({active, children, to}) => (
  <Link to={to}>
    {children}
  </Link>
);

class HistoryContents extends Component {
  render(){
    return (
      <div className="history-contents">
        <div className="history-header">
          <h2>History</h2>
        </div>
        <div className="history-band"></div>
        <div className="history-body">
          <MenuItem to={'/history/history01'}>
            <div className="history-item item02">
              <p>aaa</p>
              <span className="date">2017년 4월 5일</span>
            </div>
          </MenuItem>
          <MenuItem to={'/history/history01'}>
            <div className="history-item item01">
              <p>리액트 프로젝트를 서버에 올린 후 새로고침시 서버 오류 현상</p>
              <span className="date">2017년 4월 5일</span>
            </div>
          </MenuItem>
        </div>
      </div>
    );
  }
}

export default HistoryContents;



