import React from 'react';
import './Footer.css';

const Footer = () => {
  return (
    <footer>
      <div className="footer-wrap">
        <p>God doesn't require us to succeed. he only requires that you try.</p>
      </div>
    </footer>
  );
};

export default Footer;



