import React, { Component } from 'react';
// import SimpleMap from './SimpleMap';
import Header from '../../Header/Header';
import Contents from '../../Contents/Contents';
import Footer from '../../Footer/Footer';
import '../DailyContents.css';
import './DailyData.css';

class Data01 extends Component {
  render(){
    return (
      <div>
        <Header />
        <Contents>
          <Data />
        </Contents>
        <Footer />
      </div>
    );
  }
}

class Data extends Component {
  render(){
    return (
      <div className="daily-contents">
        <div className="daily-header">
          <h2>Daily Life</h2>
        </div>
        <div className="daily-band"></div>
        <div className="daily-body">

          { /* Data01 start */ }
          <div className="main_image">
            <img src={require('../../../images/daily/data01/data01_main.jpg')} alt="부산 감천벽화마을 이미지" />
            <p>부산 감천벽화마을에 가다</p>
            <div className="curtain"></div>
          </div>

          <div className="main_text">
            <p>
            일요일에 대구에서 사촌오빠 결혼식이 있어 이왕 대구까지 가는김에 겸사겸사 토요일에 부산에 미리가서 여행하고 다음날 일요일 대구로 이동하기로 했다.<br />
            후보지는 포항 경주 부산이였는데, 부산을 5년전에 가봤는데 좋았던 기억이 많아 다시한번 부산 가기로 결정!!<br />
            대략 일정은 토요일 아침 일찍 수원에서 출발하여 반나절동안 부산 구경하고, 일요일 아침에 대구로 이동하는 일정이다. 여행할 수 있는 시간이 반나절뿐이라 빡빡한 일정보다는 한군데 정해서 그 곳만 보기로 하였다.<br />
            그래서 가기로 결정된 곳은 부산 감천 마을이다. 블로그 찾아보니 마을을 이쁘게 꾸며논 것도 좋았고 이곳 저곳 구경할 곳이 많은 거 같아 기대기대<br />
            <br />
            그리하여 토요일 아침 일찍 언니와 같이 KTX를 타고 부산까지 2시간 40분 걸려 오전 11시에 도착했다.
            </p>

            <img src={require('../../../images/daily/data01/data01_00.jpg')} alt="부산 돼지국밥 이미지" />
            <img src={require('../../../images/daily/data01/data01_01.jpg')} alt="부산 돼지국밥 이미지" />

            <p>
            부산역 광장 바로 인근에 있는 유명한 맛집인 본전 돼지국밥집에서 돼지국밥을 맛있게 먹고 바로 감천 마을로 이동~!<br />
            <br />
            부산대병원 정류정에서 (2번대 버스) 마을버스를 타고 굽이굽이 언덕길을 거침없이 올라가는데,,, WOW.<br />
            정말 차 1대 겨우 지나갈만한 좁은 길을 기사아저씨의 거침없는 핸들링에 겁이 났지만 그만큼 내공이 있을 거라 믿으며 무사히?! 마을 입구에 도착!
            </p>

            <img src={require('../../../images/daily/data01/data01_02.jpg')} alt="부산 감천벽화마을 이미지" />

            <p>
            부산이 아무래도 남쪽이니 따뜻하겠지 생각했다면 노노노!!<br />
            수원이랑 날씨가 똑같았다. 너무 추웠다. 날씨는 맑았으나 바람도 많이 불고 으슬으슬 추웠다. 트렌치 코트를 입고 갔는데 코트를 왜 안입었을까 너무 후회됬다.<br />
            <br />
            아무튼 마을 구경 시작!
            </p>

            <img src={require('../../../images/daily/data01/data01_03.jpg')} alt="부산 감천벽화마을 이미지" />

            <p>
            마을 입구 관광안내소에서 2000원을 내면 마을 구석구석 스탬프를 찍을 수 있는 전체 지도를 준다.<br />
            지도 구입 후 사람들이 많이 가는 방향으로 걷기 시작했다.<br />
            사드 때매 중국인 관광객이 없을 거라 생각했는데, 의외로 중국인 관광객들이 많아서 조금 의아했지만 시끄럽거나 그렇친 않아서 다행이였다.
            </p>

            <img src={require('../../../images/daily/data01/data01_04.jpg')} alt="부산 감천벽화마을 이미지" />
            <img src={require('../../../images/daily/data01/data01_05.jpg')} alt="부산 감천벽화마을 이미지" />
            <img src={require('../../../images/daily/data01/data01_06.jpg')} alt="부산 감천벽화마을 이미지" />
            <img src={require('../../../images/daily/data01/data01_07.jpg')} alt="부산 감천벽화마을 이미지" />

            <p>
            감천 마을은 마을 곳곳 아름다운 색체 물감으로 물들여져있는 듯 마을 자체가 이뻤고 아름다웠다.<br />
            또한 깨알같은 소품들 하나하나도 놓치지 않고 꾸며놓은 모습을 보면서 마을 전체를 관광지로 만들기 위해 많은 노력을 했구나 느낄 수 있었다.
            </p>

            <img src={require('../../../images/daily/data01/data01_08.jpg')} alt="부산 감천벽화마을 이미지" />
            <img src={require('../../../images/daily/data01/data01_09.jpg')} alt="부산 감천벽화마을 이미지" />
            <img src={require('../../../images/daily/data01/data01_10.jpg')} alt="부산 감천벽화마을 이미지" />
            <img src={require('../../../images/daily/data01/data01_11.jpg')} alt="부산 감천벽화마을 이미지" />

            <p>
            언니한테 대단하다 이쁘다를 연발하면서 스탬프 도장도 쾅쾅 찍고 워낙 길이 굽이져서 길도 해메어 보고 어렸을 적 친척집 동네에 놀러온 것 같았다.<br />
            요새는 전부 아파트가 되어서 어린 애들은 골목에 대한 추억이 없을 수 있는데, 난 아직 골목 대장이 존재했던 세대니깐^^;;<br />
            아 맞다. 스탬프를 찍다보면 감천 마을의 사진이 담긴 3장의 엽서를 받을 수 있다.<br />
            감천 마을을 구경하면서 스탬프 찍는 재미와 엽서를 받는 행운까지!! 2000원 주고 지도를 사길 추천한다.<br />
            <br />
            감천 마을 구경을 끝내고 다시 마을버스를 타고 좁은 길을 거침없이 내려가 자갈치 시장쪽에서 하차.<br />
            걸어서 부평깡통시장이랑 국제시장? 사실 어디가 어딘지 모르겠다. 언니가 다 그근방이라고 했다. 아무튼 시장으로 바로 고!
            </p>

            <img src={require('../../../images/daily/data01/data01_12.jpg')} alt="부산 부평깡통시장 이미지" />
            <img src={require('../../../images/daily/data01/data01_13.jpg')} alt="부산 이가네 떡볶이 이미지" />
            <img src={require('../../../images/daily/data01/data01_14.jpg')} alt="부산 이가네 떡볶이 이미지" />

            <p>
            시장에 가서 제일 먼저 찾아간 곳은 백종원 삼대천왕에 나왔다던 물 안넣고 야채 육수로만 끓이는 떡볶이 맛집을 찾아갔다.<br />
            역시 입소문이 나서 그런지 사람들이 다들 줄을 서서 기다리고 있어서 언릉 맨 뒤로 가서 합류.<br />
            <br />
            줄이 금방 줄어들고 곧 우리차례가 되어 떡볶이랑 튀김 셋트 3000원짜리를 시키고 (바로 준다 ㅋㅋ) 한입 먹어보았다.<br />
            떡도 크고 양념색갈도 완전 먹음직스럽게 빨간색이여서 기대를 했지만!! 내 입맛엔 좀 짰다. 맛이 없는건 아니였다. 맛은 있었으나 짰다.<br />
            그래도 다 경험이니 생각으로ㅋㅋ 뿌듯. 튀김도 맛있었음.
            </p>

            <img src={require('../../../images/daily/data01/data01_15.jpg')} alt="비빔당면 이미지" />
            <img src={require('../../../images/daily/data01/data01_16.jpg')} alt="오뎅과 밀떡 이미지" />

            <p>
            그리고 또 살살 돌아다니면서 비빔당면과 오뎅 물떡을 사먹고, 시장구경 사람구경하고 너무 힘들어 스타벅스에서 당 보충을 하며 저녁이 되기만을 기다렸다.<br />
            <br />
            좀 이른 저녁이 되어 드디어 낙곱새를 먹으러 고고씽~!<br />
            <br />
            낙곱새는 낙지 곱창 새우가 들어간 음식으로 여러 방송에서도 많이 나왔고 그때마다 너무나도 먹음직스러워서 꼭 먹어봐야지 했던 음식중에 하나였다.<br />
            <br />
            국제시장 근처에 있는 개미집 식당에 가서 낙곱새 2인분 주문!
            </p>

            <img src={require('../../../images/daily/data01/data01_17.jpg')} alt="부산 개미네 낙곱새 음식 이미지" />
            <img src={require('../../../images/daily/data01/data01_18.jpg')} alt="부산 개미네 낙곱새 음식 이미지" />
            <img src={require('../../../images/daily/data01/data01_19.jpg')} alt="부산 개미네 낙곱새 음식 이미지" />
            <img src={require('../../../images/daily/data01/data01_20.jpg')} alt="부산 개미네 낙곱새 음식 이미지" />

            <p>
            기다림끝에 드디어 낙곱새가 나왔다. 비주얼은 오오~ 두근두근 한입 딱 먹었는데!!!!! 음... 아직 곱창을 더 익혀야 했다.<br />
            더 보글보글 끓인 후에 이번에 진짜로 제대로 한입 딱 먹어보니!!!!! 음 음.. 맛있었다. 솔직히 그렇게 특별한 맛은 아닌 다 아는 그런 맛이였다.<br />
            <br />
            그래도 밥에 비벼 먹으니 밥도둑이였다.<br />
            아쉬운 점이 있다면 곱창이 너무너무 없었다는거?<br />
            <br />
            낙지 곱창 새우를 한입에 먹으면 곱창의 기름때문에 고소하니 맛있었는데, 몇개 먹지도 않았는데 다 없어져버렸다.<br />
            글구 곱창의 퀄리티가 좀 더 좋았으면 하는 아쉬움이 남았지만 그래도 경험이니까~ 한번 먹어봤으니 이제 아쉬움은 없었다.<br />
            <br />
            모든 일정을 마치고 부산역 근처에 예약해둔 모찌 게스트하우스로 이동!
            </p>

            <img src={require('../../../images/daily/data01/data01_21.jpg')} alt="부산 모찌 게스트하우스의 모찌 강아지 이미지" />

            <p>
            모찌 게스트하우스에는 게하의 전부인 모찌라는 이름의 새끼? 코카스 파니엘 강아지가 있다.<br />
            1살 조금 넘었다고 했는데 그냥 성인견같은 ㅋㅋㅋ 친화력도 좋고 목청도 우렁차고 진짜진짜 너무 귀여웠다.<br />
            부산의 마지막 밤을 기념하기 위해 편의점에서 과자와 캔맥주를 사들고 게하에서 OCN드라마 터널 첫방송을 보며, 부산 여행 일정 마무으으리!<br />
            </p>

            <img src={require('../../../images/daily/data01/data01_22.jpg')} alt="부산 모찌 게스트하우스에서 바라본 부산 다리 야경 이미지" />

            <p>
            정말 열심히 걸어다녔고 그만큼 또 열심히 먹었다.<br />
            <br />
            너무 즐거웠고, 다음엔 더 길게 와서 부산의 못가본 곳을 다아 둘러보고 싶다.<br />
            <br />
            2017년 3월 25일 부산 여행 끄읏!
            </p>

            <div className="tags">
              <img src={require('../../../images/icon/tag.png')} alt="태그 아이콘" />
              <a href="#" rel="tag"><span>#부산여행</span></a>
              <a href="#" rel="tag"><span>#부산감천마을</span></a>
              <a href="#" rel="tag"><span>#부산당일여행</span></a>
              <a href="#" rel="tag"><span>#부평깡통시장</span></a>
              <a href="#" rel="tag"><span>#국제시장</span></a>
              <a href="#" rel="tag"><span>#부산돼지국밥맛집</span></a>
              <a href="#" rel="tag"><span>#부산이가네떡볶이</span></a>
              <a href="#" rel="tag"><span>#부산개미집</span></a>
              <a href="#" rel="tag"><span>#부산낙곱새맛집</span></a>
              <a href="#" rel="tag"><span>#모찌게스트하우스</span></a>
            </div>
          </div>
          { /* Data01 end */ }

        </div>
      </div>
    );
  }
}

export default Data01;



